body {
  margin: 0;
}
.link {
  text-decoration: none;
  transition: all 0.3s ease-in-out;
}
.link:hover {
  text-decoration: underline;
}
.countryInfoWrap {
  max-width: 320px;
  width: 100%;
  border: 1px solid #cccccc;
  padding: 15px;
  margin-top: 20px;
  box-sizing: border-box;
}
.countryInfoTitle {
  font-weight: bold;
}
