body {
  margin: 0;
}
.link {
  text-decoration: none;
  transition: all 0.3s ease-in-out;
}
.link:hover {
  text-decoration: underline;
}
.country p {
  padding: 0;
  margin: 0;
}
.countryWrap {
  display: flex;
  flex-direction: column;
  width: 100%;
  max-width: 280px;
  border: 1px solid #636363;
  margin: 15px 15px 0 15px;
  font-size: 14px;
  padding: 15px;
}
.countryTitle {
  font-weight: 600;
}
